import { VectorTile } from "@mapbox/vector-tile";
import Pbf from "pbf";

/**FreedoJs */
importScripts('../../../freedo/Freedo/Freedo.js');

const funcs = {};//
//
function regFun(funName, fun) {
    funcs[funName] = fun;
}
//
function excute(funName, args) {
    const func = funcs[funName];
    if (typeof func != 'function')
        throw new Error('method');

    const result = func(...args);
    if (result instanceof Promise)
        result.then(data => {
            postMessage({ result: data, method: funName });
        }).catch(e => console.error(e));
    else
        postMessage({ result, method: funName });
}

let urlTemplate = undefined;//pbfurl
let maximumLevel = 19;
let minimumLevel = 14;
let vectorLayers = undefined;
let targetLevel = 16;//
let cacheSize = 3000;//
const tile2Instances = {};//
let loadedInstances = [];//
const instanceSet = new Set();


self.onmessage = function (e) {
    const data = e.data;
    excute(data.method, data.args);
}

/**
 * url
 */
regFun('setAttr', setAttr);
function setAttr(opt) {
    urlTemplate = Freedo.defaultValue(opt.urlTemplate, urlTemplate);
    vectorLayers = Freedo.defaultValue(opt.vectorLayers, vectorLayers);
    minimumLevel = Freedo.defaultValue(opt.minimumLevel, minimumLevel);
    maximumLevel = Freedo.defaultValue(opt.maximumLevel, maximumLevel);
}

regFun('requestTileGeo', requestTileGeo);
function requestTileGeo(x, y, z) {
    if (z < 16) return;
    const diff = targetLevel - z;
    let maxX, maxY, minX, minY;
    if (diff > 0) {
        maxX = (x + 1) * Math.pow(2, diff);
        maxY = (y + 1) * Math.pow(2, diff);
        minX = x * Math.pow(2, diff);
        minY = y * Math.pow(2, diff);
    } else if (diff <= 0) {
        minX = parseInt(x * Math.pow(2, diff)), maxX = minX + 1;
        minY = parseInt(y * Math.pow(2, diff)), maxY = minY + 1;
    }
    return new Promise(function (resolve, reject) {
        let instances, ct = 0;
        for (let i = minX; i < maxX; i++) {
            for (let j = minY; j < maxY; j++) {
                const r = loadTileGeo([i, j, targetLevel]);
                if (r instanceof Promise) {
                    ct++;
                    r.then(e => {
                        ct--;
                        if (ct == 0) resolve(e)
                    });
                }
                else instances = r;
            }
        }
        if (ct == 0) resolve(instances);
    });
}

regFun('loadTileGeo', loadTileGeo);
function loadTileGeo(xyz, url = parseUrl(xyz)) {
    if (!url) return new Error('urlTemplateurl');
    if (xyz[2] != 16) return loadedInstances;
    const instances = tile2Instances[xyz.join(',')];
    if (!instances) {
        return new Promise(function (resolve, reject) {
            Freedo.Resource.fetchArrayBuffer({ url }).then(data => {
                if (!data) reject();
                const vt = new VectorTile(new Pbf(data));
                parse2Instances(vt.layers, ...xyz);
                resolve(loadTileGeo(xyz, url));
            }).otherwise(e => reject(e));
        });
    }
    if (instances.length < 1) return loadedInstances;
    instances.forEach(e => instanceSet.add(e));
    loadedInstances = Array.from(instanceSet);
    // loadedInstances.push(...instances);
    // loadedInstances = trimRepeatTile(...xyz, loadedInstances);
    trimCachedGeometry(loadedInstances);
    console.log(xyz[2]);

    return loadedInstances;
}

function parse2Instances(vLayers, x, y, z) {
    const keyStr = `${x},${y},${z}`;
    let layers = Object.values(vLayers);
    if (vectorLayers)
        layers = layers.filter(e => vectorLayers.indexOf(e.name) > -1);

    const geoInstances = [];
    for (const layer of layers) {
        for (let i = 0; i < layer.length; i++) {
            const feature = layer.feature(i);
            const geoJson = feature.toGeoJSON(x, y, z);
            geoInstances.push(...parseGeoJson(geoJson));
        }
    }
    tile2Instances[keyStr] = geoInstances;//
}

regFun('parseGeoJsons', parseGeoJsons);
function parseGeoJsons(jsons, xyz) {
    const geoInstances = [];
    for (let i = 0; i < jsons.length; i++) {
        const geoJson = jsons[i];
        geoInstances.push(...parseGeoJson(geoJson));
    }
    return { geoInstances, xyz };
}

function parseGeoJson(geoJson) {
    const geometry = geoJson.geometry,
        coords = geometry.coordinates,
        height = geoJson.properties.height,
        geoInstances = [];
    switch (geometry.type) {
        case "Polygon":
            geoInstances.push(createPolygonGeoInstance(coords, height));
            break;
        case "MultiPlygon":
            for (const coord of coords)
                geoInstances.push(createPolygonGeoInstance(coords, height));
            break;
        default:
            break;
    }
    return geoInstances;
}

function createPolygonGeoInstance(coords, height = Math.random() * 200) {
    const pts = coords.shift().map(e => Freedo.Cartesian3.fromDegrees(...e)),
        holes = [];
    const polygonHierarchy = new Freedo.PolygonHierarchy(pts, holes);
    for (const coord of coords) {
        const pts = coord.map(e => Freedo.Cartesian3.fromDegrees(...e));
        holes.push(new Freedo.PolygonHierarchy(pts));
    }
    const geo = new Freedo.PolygonGeometry({
        polygonHierarchy, extrudedHeight: height
    });
    const geometry = Freedo.PolygonGeometry.createGeometry(geo);
    return new Freedo.GeometryInstance({
        geometry
    });
    // return geometry;
}
regFun('test', test);
function test(args) {
    return args;
}

/**
 * 
 * @param {Freedo.GeometryInstance[]} geoInstances 
 */
function trimCachedGeometry(geoInstances) {
    while (geoInstances.length > cacheSize)
        geoInstances.shift();
}

function trimRepeatTile(x, y, z, geoInstances) {
    traversalParentTile(x, y, z, callback);
    traversalChildTile(x, y, z, callback);

    function callback(x, y, z) {
        const geos = tile2Instances[`${x},${y},${z}`];
        if (!geos) return;
        geoInstances = geoInstances.filter(e => geos.indexOf(e) < 0);
    }
    return geoInstances;
}

/**
 * 
 * @param {number} x 
 * @param {number} y 
 * @param {number} z 
 * @param {function} callback 
 */
function traversalParentTile(x, y, z, callback) {
    if (z <= minimumLevel) return;
    console.log(z, minimumLevel, new Date().getMilliseconds());
    const pX = parseInt(x / 2),
        pY = parseInt(y / 2),
        pZ = z - 1;
    if (typeof callback == 'function') callback(pX, pY, pZ);
    traversalParentTile(pX, pY, pZ, callback);
}

function parseUrl(xyz) {
    if (!urlTemplate) return;
    return urlTemplate.replace('{x}', xyz[0]).replace('{y}', xyz[1]).replace('{z}', xyz[2]);
}

/**
 * 
 * @param {number} x 
 * @param {number} y 
 * @param {number} z 
 * @param {function} callback 
 */
function traversalChildTile(x, y, z, callback) {
    if (z >= maximumLevel) return;
    const x0 = x * 2, x1 = x * 2 + 1,
        y0 = y * 2, y1 = y * 2 + 1;
    z += 1;
    callback(x0, y0, z);
    callback(x0, y1, z);
    callback(x1, y0, z);
    callback(x1, y1, z);
    console.log(z, maximumLevel, new Date().getMilliseconds());

    traversalChildTile(x0, y0, z, callback);
    traversalChildTile(x0, y1, z, callback);
    traversalChildTile(x1, y0, z, callback);
    traversalChildTile(x1, y1, z, callback);
}
